.CommonButton {
  color: #fff;
  background-color: #343a40;
  border-color: #343a40;
  margin-top: 10px;
  height: 50px;
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  line-height: 1.5;
  border-radius: 0.3rem;
  width: 100%;
}
.CommonButton:hover {
  color: #fff !important;
  background-color: #23272b !important;
  border-color: #1d2124 !important;
}
.width-48 {
  width: 48%;
}
.redSend {
  color: red;
  height: 50px;
  padding: 0.5rem 1rem;
  font-size: 80%;
  line-height: 1.5;
  border-radius: 0.3rem;
  margin-bottom: 50px;
  text-decoration: none;
}

.redSend:hover {
  background-color: none;
  text-decoration: underline;
  color: red;
}
.link2 {
  margin-top: 10px;
  font-size: 15px;
  font-weight: 400;
  text-decoration: none;
  color: red;
}
.regiserLink-1:hover {
  text-decoration: underline !important;
  color: red;
}
.PhoneInput input {
  height: 50px;
  padding: 0.5rem 1rem;
  font-size: 1.25rem;
  line-height: 1.5;
  border-radius: 0.3rem;
  border: 1px solid #ced4da;
}
.PhoneInput input:focus {
  color: #495057;
  background-color: #fff;
  border-color: #80bdff;
  outline: 0;
  box-shadow: 0 0 0 0.2rem rgb(0 123 255 / 25%);
}
.dropDwon {
  height: 40px;
  width: 100%;
  border: 1px solid #ced4da;
}
.form-check-label {
  color: #000 !important;
  font-weight: 500;
}

.qr-notice {
  font-weight: bolder;
}

.qr-notice li span {
  font-weight: normal;
}
.bold {
  font-weight: bold;
}
.hieght-50px {
  height: 50px;
}
.loginLinks {
  margin-top: 10px;
  font-size: 20px;
  font-weight: bold;
  text-decoration: none;
  color: red;
}
.loginLinks:hover {
  color: red;
  text-decoration: underline;
}
